import React from 'react';
import {Link} from 'react-router-dom';

const header = () => {
    return (
        <div className="header row">
            <div className="logo">
                <Link to="/">
                    <img src="./images/logo_b.png" alt="maison de fleurs 로고"/>
                </Link>
            </div>
            <nav id="nav">
                <div><Link to="/flower">Flower</Link></div>
                <div><Link to="/flower">Flower</Link></div>
                <div><Link to="/flower">Flower</Link></div>
            </nav>
        </div>
    );
};

export default header;