import React, { Component } from 'react';
import Header from '../components/header';
import Footer from '../components/footer';

class flower extends Component {
    render() {
        return (
            <div>
                <Header/>
                <section className="row">
                    <div className="tab">
                        <div data-filter="*">All</div>
                        <div data-filter="friends">Friends</div>
                        <div data-filter="parents">Parents</div>
                        <div data-filter="christmas">Christmas</div>
                    </div>
                    <ul>
                        <li data-value="parents"><img src="./images/f1.png" alt="꽃"/></li>
                        <li data-value="parents"><img src="./images/f2.png" alt="꽃"/></li>
                        <li data-value="christmas"><img src="./images/f3.png" alt="꽃"/></li>
                        <li data-value="christmas"><img src="./images/f4.png" alt="꽃"/></li>
                        <li data-value="friends"><img src="./images/f5.png" alt="꽃"/></li>
                        <li data-value="parents"><img src="./images/f6.png" alt="꽃"/></li>
                        <li data-value="friends"><img src="./images/f7.png" alt="꽃"/></li>
                    </ul>
                </section>
                <Footer/>
            </div>
        );
    }
}

export default flower;